/* ==========================================================================
   GLOBAL STYLES
   ========================================================================== */
/* FONTS */

/* Quicksand */

@font-face {
    font-family: 'Quicksand';
    src: url('../assets/fonts/quicksand-regular-webfont.woff2') format('woff2'),
         url('../assets/fonts/quicksand-regular-webfont.woff') format('woff'),
         url('../assets/fonts/quicksand-regular-webfont.ttf')  format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Quicksand';
    src: url('../assets/fonts/quicksand-light-webfont.woff2') format('woff2'),
         url('../assets/fonts/quicksand-light-webfont.woff') format('woff'),
         url('../assets/fonts/quicksand-light-webfont.ttf')  format('truetype');
    font-weight: 200;
    font-style: normal;
}

@font-face {
    font-family: 'Quicksand';
    src: url('../assets/fonts/quicksand-medium-webfont.woff2') format('woff2'),
         url('../assets/fonts/quicksand-medium-webfont.woff') format('woff'),
         url('../assets/fonts/quicksand-medium-webfont.ttf')  format('truetype');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Quicksand';
    src: url('../assets/fonts/quicksand-bold-webfont.woof2') format('woff2'),
         url('../assets/fonts/quicksand-bold-webfont.woof') format('woff'),
         url('../assets/fonts/quicksand-bold-webfont.ttf')  format('truetype');
    font-weight: bold;
    font-style: normal;
}

/* Box Sizing
  ========================================================================== */

html {
  -moz-box-sizing: border-box;
       box-sizing: border-box;
  font-size: 100%;
}

*,
*:after,
*:before {
  -moz-box-sizing: inherit;
       box-sizing: inherit;
}


/* FONTS & MARGINS
  ========================================================================== */

body {
  background-color: #ffffff;
  font-family: 'Quicksand',
              "Segoe UI",  
              "Helvetica Neue", 
              sans-serif;
  font-size: 15px;
  font-size: -webkit-calc(15px + 6 * ((100vw - 380px) / 760));
  font-size: calc(15px + 6 * ((100vw - 380px) / 760));
  line-height: 1.6;
  min-height: 100%;
  position: relative;
  padding-bottom: 70px;
  font-weight: normal;
  font-style: normal;
}

@media screen and (min-width: 1140px) {

    body {
        font-size: 21px;
    }
}

@media screen and (max-width: 380px) {

    body {
        font-size: 15px;
    }
}

em {
   font-style: italic;
}

strong {
   font-weight: bold;
}

/* PARAGRAPHS
  ========================================================================== */

p {
  font-size: 1em;
  max-width: 36em;
  color: #0A0A0A;
  margin: 0;
  -webkit-hyphens: auto;
     -moz-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto;
}

/* HEADINGS
  ========================================================================== */

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 21px 0 0;
  /* non-scalable fallback for old browsers */
  margin-top: 1.5rem;
  /* 21px, will be ignored by old browsers */
}

/* LINKS
  ========================================================================== */

a {
  text-decoration: none;
}

a:link {
  color: #ff6732;
}

a:visited {
  color: #ff6732;
}

a:focus,
a:hover {
  border-bottom: 1px solid #ff6732;
}

a:active {
  color: #ff9d00;
}

/* IMAGES
  ========================================================================== */

img {
  height: auto;
  width: auto;
  /* max-width allows image to be responsive. */
  max-width: 100%;
}

/* RESET MARGIN & PADDING TO 0.
  ========================================================================== */

header,
main,
section {
  margin: 1.5rem 0 0;
  padding: 0;
}

/* WRAPPER div
  ========================================================================== */
.wrap {
  width: 95%;
  max-width: 71.25em;
  margin: 0 auto;
}

/* LISTS
  ========================================================================== */
li,
ul {
  margin: 0;
  padding: 0;
}

/* ==========================================================================
   MOBILE STYLES
   ========================================================================== */

/* HEADER
  ========================================================================== */

header h1 {
  padding: 0;
}

header h1 a {
  display: block;
  width: 100%;
  text-align: center;
}

header h1 a:link {
  color: #000;
}

header h1 a:visited {
  color: #000;
}

header h1 a:focus,
header h1 a:hover {
  border-bottom: none;
  color: #00BCD4;
}

/* HEADER - NAVIGATION
  ========================================================================== */

nav {
  margin-top: 1rem;
}

nav a {
  display: block;
  font-size: 1.1em;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  padding: 1em 0;
  margin: 0;
  letter-spacing: 0.12em;
}

nav a:link {
  color: #ff6732;
}

nav a:visited {
  color: #ff6732;
}

nav a:focus,
nav a:hover {
  border-bottom: none;
  color: #00BCD4;
}

/* SECTION - ABOUT
  ========================================================================== */

.about {
  width: 100%;
  max-width: 35em;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 3px;
  border: 3px solid palegoldenrod;
  box-shadow: 0 20px 30px 0 rgba(36,50,66,.14);
  height: 100%;
}

.avatar,
.bio {
  margin: 1.5rem 0 0;
  padding: 0;
  width: 100%;
}

.avatar div {
  width: 90px;
  height: 130px;
  margin: 3em auto 0;
}

.avatar img {
width: 90px;
height: 130px;
}

.bio > p {
  max-width: 35em;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0 1em;
}

.bio > * + * {
  margin-top: 1.1rem;
}

/* SECTION - SOCIAL
  ========================================================================== */

.social-m {
  margin: 1.5rem 0 0;
  padding: 1em 0;
  width: 100%;
}

.social-m a {
  display: inline-block;
  margin: 0;
  padding: 0.1em;
  width: 23%;
  text-align: center;
}

.social-m a:last-child {
  border-right: none;
}

.social-m a:link {
  color: #ff6732;
}

.social-m a:visited {
  color: #ff6732;
}

.social-m a:focus,
.social-m a:hover {
  color: #00BCD4;
  border-bottom: none;
}

main {
  padding-bottom: 1.5rem;
}

/* FOOTER
  ========================================================================== */

footer {
  margin: 0;
  padding: 0;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 70px;
  background-color: none;
}

footer > p + p {
  margin-top: 0.5em;
}

footer p a:hover, footer p a:focus {
  color: #00BCD4;
  border-bottom: 1px solid #00BCD4;
}

.half,
.one,
.one-fourth,
.one-third,
.two-thirds {
  width: 100%;
}

/* BLOG PAGE STYLES
  ========================================================================== */

.blog-wrap {
  max-width: 42em;
  margin: 0 auto;
}

.blog-main {
  margin-top: 3rem;
}

.blog-list {
  list-style: none;
  padding: 1rem;
  max-width: 45em;
  margin: 0 auto;
}

.blog-list a:hover, .blog-list a:focus {
  border-bottom: 1px solid #00BCD4;
  color: #00BCD4;
}
.blog-list li {
  font-size: 1.2em;
  text-align: left;
  margin-top: 1.2em;
}

.blog-list li time {
  font-size: 0.6em;
  font-weight: 700;
  padding: 2px 5px;
  background-color: #EEE9AE;
  color: #000;
  margin-right: 1em;
}

.post-wrap {
  max-width: 40em;
  margin: 0 auto 100px;
}

/* BLOG POST LINKS
  ========================================================================== */

.post-wrap a:link {
  color: #ff6732;
}

.post-wrap a:visited {
  color: #ff6732;
}

.post-wrap a:focus,
.post-wrap a:hover {
  border-bottom: 2px solid #00bcd4;
  color: #00BCD4;
}

.post-wrap a:active {
  color: #ff9d00;
}

.post-article {
  max-width: 45em;
  margin: 4rem auto 0;
  line-height: 1.8;
  padding: 0 0.25rem;
}

.post-article p {
  max-width: 100%;
}

p.post-date {
  font-size: 0.75em;
  font-weight: 700;
  max-width: 200px;
  margin: 0.5rem 0 4rem;
  padding: 0 10px;
  background-color: #EEE9AE;
  color: #000;
}

/** Introduces Vertical Rhythm.
*   Whitespace above subhead is larger than whitespace below subhead
*/
.post-article p + h2 {
  margin-top: 2.1rem;
}

.post-article h2 + p {
  margin-top: 0.7rem;
}

.post-article p + h3 {
  margin-top: 2.1rem;
}

.post-article h3 + p {
  margin-top: 0.7rem;
}

.post-article p + h4 {
  margin-top: 2.1rem;
}

.post-article h4 + p {
  margin-top: 0.7rem;
}

/** Introduces Vertical Rhythm.
*   Whitespace above list items.
*/
.post-article ul,
li {
  margin-top: 1.05rem;
  padding: 0;
}

.post-article ul p,
li p {
  margin: 0;
  padding: 0;
}

.post-article ul {
  margin-left: 1rem;
  list-style-position: outside;
}

.post-article li + li {
  margin-top: 1.5rem;
}

.post-article p + p {
  margin-top: 1rem;
}

.post-article ul {
  margin-left: 2.5rem;
}

.post-article ul + p {
  margin-top: 0.8rem;
}

/* HEADING COLOR
  ========================================================================== */

.post-wrap h1,
h2,
h3,
h4,
h5,
h6 {
  color: #079FC0;
}

/* BLOCKQUOTE
  ========================================================================== */

.post-article blockquote {
  font-style: italic;
  margin: 1.5em 0;
  border-left: 2px solid #ff6732;
  padding: 0.75em 0 0 1.5em;
}

/* 'pre' Element
  ========================================================================== */

pre {
  display: block;
  padding: 9.5px;
  margin: 0 0 10px;
  font-size: 1em;
  line-height: 1.5;
  color: #333;
  word-break: break-all;
  word-wrap: break-word;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* 'code' Element
  ========================================================================== */

code {
  font-family: monospace;
  padding: 2px 4px;
  font-size: 100%;
  color: #333;
  border-radius: 4px;
  /*background-color: #eff0f1;*/
}

/* HIGHLIGHTING ELEMENTS
  ========================================================================== */

.post-article .highlighter-rouge {
  font-size: 0.75em;
  padding: 0.1em 0.4em;
  margin: 0 0.2em;
  background-color: #fafafa;
  color: rgb(0, 0, 0);
  box-shadow: 0 1px 2px #888888;
  position: relative;
}

.post-article div[class^="language-"] {
  background-color: #ffffff;
  box-shadow: none;
  border: 0;
  padding: 0;
  margin-bottom: 1em;
  margin-top: 1em;
}

.highlight {
  box-shadow: inset 0 1px 0 #fff, 1px 1px 0 #fff, 2px 2px 0 #ddd, 3px 3px 0 #fff, 4px 4px 0 #ddd;
  border-radius: 4px;
}

/* Following code will remove semicolon highlighting. */
.highlight .err {
  background-color: transparent;
}

.post-article .highlight pre,
.post-article pre.highlight {
  overflow-x: auto;
}

.post-article .highlight pre code,
.post-article pre.highlight code {
  overflow-wrap: normal;
  white-space: pre;
}

.post-article .highlighter-rouge:before {
  content: attr(data-title);
  position: absolute;
  top: 0;
  left: 0;
  right: 20px;
  text-align: right;
  color: #104359;
  font-weight: 500;
  letter-spacing: 0.8px;
  text-transform: uppercase;
  font-family: monospace;
}

/* 'Note:' Highlight
  ========================================================================== */

.post-article .note {
  background-color: rgba(233, 233, 144, 0.82);
  padding-left: 1em;
}


/* Horizontal Rule
  ========================================================================== */
.line {
  width: 100%;
  border-top: 4px solid #FDDE9F;
  margin: 2em 0 4em 0;
}

/* DISQUS
  ========================================================================== */
.disqus {
  margin-top: 100px;
  max-width: 45em;
  margin: 0 auto;
}


/* ==========================================================================
   TAB STYLES
   ========================================================================== */

@media screen and (min-width: 40em) {

  /* WRAPPER div
  ========================================================================== */

  .wrap {
    width: 95%;
    max-width: 60em;
    margin: 0 auto;
  }

