  }
  
  /* SECTION -SOCIAL
  ========================================================================== */

  .social-m {
    padding: 1em 0;
    max-width: 25em;
    margin: 2em auto 0;
    border: none;
  }
  
  /* FOOTER
  ========================================================================== */

  footer p {
    text-align: center;
    margin: 0 auto;
  }

  footer small {
    font-size: 65%;
  }
}

/* ==========================================================================
   DESKTOP STYLES
